*{
    font-family: sans-serif;
    font-size: 18px;
  }
  /* body{
    display: flex;
    justify-content: center;
    align-items: center;
  } */
  .container{
    top: 37%;
    left:50%;
    position: absolute;
    transform: translate(-50%, -50%);
    margin: 0,auto;
    width: 256px;
    text-align: center;
    border: 10px solid black;
    background: black;
    border-radius: 10px;
  }
  .keypad{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(60px,auto);
  }
  input[type="text"]{
    width: 249px;
    height: 75px;
    background: black;
    border: 0px;
    color:white;
    text-align: right;
    font-size: 25px;
    font-weight: 500;
    letter-spacing: 1px;
  }
  #clear{
    grid-column: 1/3;
    grid-row: 1;
    background-color: aquamarine;
  }
  #equal{
    grid-column: 3/5;
    grid-row: 5;
    background-color: aquamarine;
  }
  button{
    margin: 3px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
  }
  input:focus,
  input:active,
  button:focus,
  button:active{
    outline: none;
  }